@import "../../../style/variables";

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 175px;

  &__left-arrow {
    margin-right: 18px;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__item {
    width: 19px;
    height: 19px;
    border-radius: 50%;
    background-color: $main-color;
    margin-right: 18px;
    opacity: 0.1;

    &--active {
      opacity: 1;
    }
  }
}
